import  vue from 'vue';

import  { firebaseDb , firebaseAuth } from "../boot/firebase";
import {uid} from 'quasar';

let messageRef;

const state = {
  userDetails : {},
  users       :{},
  messages    :{},
  businessList: [],
  records: [],
};


const mutations ={
    setUserDetails(state , payload){
        state.userDetails = payload;
    },
    addBusiness(state,payload) {
      state.businessList.push(payload)
    },
    addUser(state,payload){
      vue.set(state.users , payload.userId , payload.userDetails);
    },
    addRecord(state,payload) {
      state.records.push(payload)
    },
    updateUser(state, payload){
      Object.assign(state.users[payload.userId], payload.userDetails)
    },
    addMessage(state, payload){
      vue.set(state.messages, payload.messageId , payload.messageDetails)
    },
    clearMessages(){
      state.messages ={}
    }
};


const actions ={


    registerUser({},payload){
      firebaseAuth.createUserWithEmailAndPassword(payload.email, payload.password)
        .then(response  => {
          let userId = firebaseAuth.currentUser.uid;
          firebaseDb.ref('users/' + userId ).set({
            name : payload.name,
            email : payload.email,
            password : payload.password,
            online : true,
          })

        })
        .catch(error =>{
          console.log(error.message)
        })
    },
    loginUser({},payload){
      firebaseAuth.signInWithEmailAndPassword(payload.email, payload.password)
        .then(response  => {
          let userId = firebaseAuth.currentUser.uid;

        })
        .catch(error =>{
          console.log(error.message)
        })
    },
    logoutUser(){
        firebaseAuth.signOut();
    },
    handleAuthChange({commit ,dispatch , state }){


          firebaseAuth.onAuthStateChanged(user => {
            if (user) {
              // User is logged in.
              let userId = firebaseAuth.currentUser.uid
              firebaseDb.ref('users/' + userId).once('value', snapshot => {
                let userDetails = snapshot.val()
                commit('setUserDetails', {
                  name: userDetails.name,
                  email: userDetails.email,
                  userId: userId
                })
              })
              dispatch('firebaseUpdateUser', {
                userId: userId,
                updates: {
                  online: true
                }
              })
              dispatch('firebaseGetUsers')
              this.$router.push('/')

        }else{
          // User is signed out.

          dispatch('firebaseUpdateUser',{
                   userId: state.userDetails.userId,
                   updates:{
                        online :false
                   }
              })
              commit('setUserDetails',{});
              this.$router.replace('/auth')
          }
      });
      console.log('state changed')
    },

    firebaseUpdateUser({}, payload){
      if (payload.userId){
        firebaseDb.ref('users/' +payload.userId).update(payload.updates)

      }
    },

    firebaseGetUsers({ commit }, payload){

      firebaseDb.ref('users/').on('child_added', snapshot =>{
        let userDetails = snapshot.val();
        let userId = snapshot.key;
        commit('addUser',{
          userId,
          userDetails
        })
      })

      firebaseDb.ref('users/').on('child_changed', snapshot =>{
        let userDetails = snapshot.val();
        let userId = snapshot.key;
        commit('updateUser',{
          userId,
          userDetails
        })
      })

    },

    firebaseGetMessages({commit,state}, otherUserId){

      let userId = state.userDetails.userId
      messageRef = firebaseDb.ref('chats/' +  userId + '/' + otherUserId)
      messageRef.on('child_added', snapshot =>{


        let messageDetails = snapshot.val()
        let messageId = snapshot.key
        commit('addMessage',{
          messageId,
          messageDetails
        })

      })
    },
    firebaseStopGettingMessages({commit}){

      if (messageRef){
        messageRef.off('child_added')
      }
      commit('clearMessages')
      console.log('uid ');
    },
    firebaseSendMessage({commit,state},payload){

      let userId = state.userDetails.userId
      payload.message.created = Date.now()

      messageRef = firebaseDb.ref('chats/' +  userId + '/' + payload.otherUserId).push(payload.message)

      payload.message.from ='them'
      messageRef = firebaseDb.ref('chats/' + payload.otherUserId  + '/' +  userId).push(payload.message)


      console.log(payload)
    },

    addBusiness({ commit },businessData) {

      let businessId = uid();
      businessData.id = businessId;
      let payload =  businessData;

     commit('addBusiness', payload);
    },
    addRecord({ commit },recordData) {
      console.log('x');
      let rcordId = uid();
      let payload =recordData;
      console.log(payload)
     commit('addRecord', payload);
    }

};

const getters ={
  users: state=>{
    let usersFiltered = {}
    Object.keys(state.users).forEach( key => {
      if(key != state.userDetails.userId){
        usersFiltered[key] = state.users[key]
      }
    })
    return usersFiltered
  },
  getBusinessList: (state)=>{
    return state.businessList;
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}




